import CloseIcon from "@mui/icons-material/CloseOutlined";
import SearchIcon from "@mui/icons-material/SearchOutlined";
import NoResultItem from "@mui/icons-material/WarningOutlined";
import {
    Avatar,
    Box,
    Button, CircularProgress, Dialog,
    DialogActions,
    DialogTitle, Divider, IconButton, InputAdornment, styled, Table, TableBody, TableCell,
    TableContainer, TableRow
} from "@mui/material";
import { useEffect, useState } from "react";
import useDebounce from '../../../utils/useDebounce';
import WidgetsOutlined from "@mui/icons-material/WidgetsOutlined";
import { useMiddleware } from "../../../Application";
import TextField from "../../../components/TextField";
import ResponsiveDialog from "../../../components/ResponsiveDialog";


const StyledTableRow = styled(TableRow)(({ theme, hover, selected }) => ({
    cursor: hover ? 'pointer' : 'auto',
    backgroundColor: selected ? theme.palette.action.hover : theme.palette.common.white,
    '&:focus': {
        backgroundColor: theme.palette.action.hover,
    },
    '&:last-child td, &:last-child th': {
        border: 0,
    },
}));

const ModulesTable = ({
    loading,
    modules,
    selected,
    onSelect,
    selectable
}) => {

    const isSelected = (module) => {
        if (!selectable || !selected)
            return false;

        if (Array.isArray(selected))
            return selected.filter(m => m.id === module.id).length > 0;

        return selected.id === module.id;
    }

    if (loading) {
        return (
            <Box sx={{ px: 5, py: 5, textAlign: "center" }}>
                <CircularProgress />
            </Box>
        )
    }

    if (modules.length < 1) {
        return (
            <Box sx={{ px: 5, py: 5, textAlign: "center" }}>
                <NoResultItem fontSize="large" />
                <p>موردی جهت نمایش وجود ندارد.</p>
            </Box>
        )
    }


    return (
        <TableContainer sx={{ overflow: "auto", maxHeight: 400 }}>
            <Table stickyHeader size="small">
                <TableBody>
                    {modules.map((module, index) => (
                        <StyledTableRow
                            key={index}
                            selected={isSelected(module)}
                            hover={selectable}
                            onClick={() => selectable && onSelect(module)}
                        >
                            <TableCell align="center">{index + 1}</TableCell>
                            <TableCell>
                                <Box sx={{ display: 'flex' }}>
                                    <Avatar src={module.picture} variant="rounded">
                                        <WidgetsOutlined />
                                    </Avatar>
                                    <Box sx={{ pl: 2 }}>
                                        <strong>{module.name}</strong>
                                        <br />
                                        <Box sx={{ fontFamily: 'Helvetica' }}>{module.identifier}</Box>
                                    </Box>
                                </Box>
                            </TableCell>
                        </StyledTableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer >
    )
}

const ModulesDialog = ({
    open,
    onClose,
    selectable,
    selected,
    onSelect,
    onClear,
    queryParams = {},
}) => {

    const middleware = useMiddleware('core.modules');

    const [loading, setLoading] = useState(true);
    const [modules, setModules] = useState([]);

    const [query, setQuery] = useState('');


    const debouncedQuery = useDebounce(query, 500);

    const loadModulesAsync = async () => {
        const { ok, data } = await middleware.list({
            ...queryParams,
            query: debouncedQuery,
            ...(query === '' ? {
                page: 0,
                perPage: 100
            } : {})
        });

        if (ok) {
            setModules(data);
            setLoading(false);
        }
    }

    useEffect(() => {
        if (open) {
            loadModulesAsync();
        } else {
            const timer = setTimeout(() => {
                setLoading(true);
                if (query !== '') setQuery('');
            }, 500);

            return () => {
                clearTimeout(timer);
            }
        }
    }, [open, debouncedQuery]);


    return (
        <ResponsiveDialog
            open={open}
            onClose={onClose}
            dialogProps={{
                fullWidth: true,
                maxWidth: "xs"
            }}
        >
            <DialogTitle sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Box>{selectable ? 'انتخاب ماژول' : 'مدیریت ماژول‌ها'}</Box>
                <IconButton onClick={onClose} size="small">
                    <CloseIcon />
                </IconButton>
            </DialogTitle>
            <Divider />
            <Box sx={{ p: 2 }}>
                <TextField
                    InputLabelProps={{ shrink: true }}
                    fullWidth={true}
                    size="small"
                    variant="filled"
                    label="جستجو براساس نام"
                    value={query}
                    onChange={(e) => setQuery(e.target.value)}
                    // autoFocus
                    InputProps={{
                        startAdornment: <InputAdornment position="start"><SearchIcon /></InputAdornment>
                    }}
                />
            </Box>
            <ModulesTable
                loading={loading}
                modules={modules}
                selectable={selectable}
                selected={selected}
                onSelect={onSelect}
            />
            <Divider />
            <DialogActions>
                {selectable && (
                    <>
                        <Button type="button" onClick={onClose}>بستن</Button>
                        <Box sx={{ flexGrow: 1 }}></Box>
                    </>
                )}
                {onClear && <Button type="button" onClick={onClear}>پاک کردن</Button>}
                {!selectable && (
                    <>
                        <Box sx={{ flexGrow: 1 }}></Box>
                        <Button type="button" onClick={onClose}>انصراف</Button>
                    </>
                )}
            </DialogActions>
        </ResponsiveDialog>
    )
}

export default ModulesDialog