import SaveIcon from "@mui/icons-material/Save";
import { LoadingButton } from "@mui/lab";
import {
    Alert,
    Box,
    Button, Dialog,
    DialogActions,
    DialogTitle, Divider, Grid,
    Paper,
    Stack,
    styled,
    Typography
} from "@mui/material";
import forOwn from "lodash/forOwn";
import map from "lodash/map";
import { useSnackbar } from "notistack";
import { useEffect, useState } from "react";
import { useForm, useWatch } from "react-hook-form";
import { useMiddleware } from "../../../Application";
import TextField from "../../../components/TextField";
import useDialog from "../../../utils/useDialog";
import ModulesDialog from "./ModulesDialog";

const PERIODS = [
    { name: "آزمایشی", value: "demo" },
    { name: "شش‌ ماهه", value: "semi-annually" },
    { name: "یک‌ ساله", value: "annually" },
    { name: "مادام‌العمر", value: "lifetime" },
]

const LabelItem = styled(Paper, {
    shouldForwardProp: prop => prop !== 'selected'
})(({ theme, selected }) => ({
    display: 'inline-block',
    padding: theme.spacing(1, 3),
    cursor: "pointer",
    outline: '1px solid transparent',
    transition: theme.transitions.create('all'),

    ...(selected && ({
        borderColor: theme.palette.primary.main,
        outlineColor: theme.palette.primary.main,
        '& .MuiTypography-root': {
            fontWeight: 700
        }
    }))
}));


const WorkspaceModulesDialog = ({
    open,
    onClose,
    workspace,
    onSave = () => {}
}) => {

    const middleware = useMiddleware('core.workspaces');

    const defaultValues = {
        modules: [],
        period: 'annually',
    }

    const { control, register, handleSubmit, formState: { errors }, setValue, reset, setError } = useForm({
        defaultValues
    });

    const period = useWatch({ control, name: "period" });
    const modules = useWatch({ control, name: "modules" });

    const [isSaving, setSaving] = useState(false);

    useEffect(() => {
        if (!open) {
            const timer = setTimeout(() => {
                reset(defaultValues);
            }, 500);

            return () => {
                clearTimeout(timer);
            }
        } else {
            // if (workspace) reset(workspace);
        }
    }, [open, workspace])

    const { enqueueSnackbar } = useSnackbar();

    const _saveAsync = async (data) => {
        setSaving(true);

        const res = await middleware.subscribe(workspace.id, {
            ...data,
            modules: map(data.modules, 'id')
        });
        if (res.ok) {
            enqueueSnackbar("میز کار بروزرسانی گردید.")
            onClose()
            onSave()
            reset(defaultValues)
        } else {
            if (res.status === 422) {
                forOwn(res.errors, (value, key) => {
                    setError(key, { type: "manual", message: value[0] })
                })
            }
        }
        setSaving(false);
    }

    const {
        isOpen: isModulesDialogOpen,
        open: openModulesDialog,
        close: closeModulesDialog
    } = useDialog();

    return (
        <Dialog
            open={open}
            onClose={onClose}
            fullWidth
            maxWidth="sm"
        >
            <DialogTitle>
                {'افزودن برنامه جدید'}
            </DialogTitle>
            <Divider />
            <Box sx={{ p: 2 }}>
                <Alert variant="outlined" color="warning" sx={{ mb: 2 }}>
                    پس از افزودن برنامه‌های جدید به میز کار، حتما سرور باید ریستارت گردد.
                </Alert>
                <Grid container spacing={2}>
                    <Grid item xs={12} md={12}>
                        <TextField
                            InputLabelProps={{ shrink: true }}
                            fullWidth={true}
                            size="small"
                            label="برنامه‌ها"
                            variant="filled"
                            value={modules ? map(modules, 'name') : ''}
                            onChange={() => { }}
                            inputProps={{
                                readOnly: true
                            }}
                            onFocus={() => openModulesDialog()}
                            onClick={() => openModulesDialog()}
                            onKeyDown={e => e.keyCode === 13 && openModulesDialog()}
                        />
                    </Grid>
                    <Grid item xs={12} md={12} justifyContent="center" display="flex">
                        <Stack gap={1} flexDirection="row">
                            {PERIODS.map((_period, index) => (
                                <LabelItem
                                    key={index}
                                    variant="outlined"
                                    selected={period === _period.value}
                                    onClick={() => setValue("period", _period.value)}
                                >
                                    <Typography>{_period.name}</Typography>
                                </LabelItem>
                            ))}
                        </Stack>
                    </Grid>
                </Grid>
            </Box>
            <Divider />
            <DialogActions>
                <LoadingButton
                    loading={isSaving}
                    loadingPosition="start"
                    type="submit"
                    variant="contained"
                    startIcon={<SaveIcon />}
                    onClick={handleSubmit(_saveAsync)}
                >
                    ثبت
                </LoadingButton>
                <Box sx={{ flexGrow: 1 }}></Box>
                <Button type="button" onClick={onClose}>انصراف</Button>
            </DialogActions>

            <ModulesDialog
                open={isModulesDialogOpen}
                onClose={closeModulesDialog}
                // label="workspace"
                selectable={true}
                onClear={() => {
                    setValue("modules", '');
                    closeModulesDialog();
                }}
                onSelect={newModule => {
                    setValue('modules', [...modules, newModule]);
                    closeModulesDialog();
                }}
                selected={modules}
                // requestConfig={{
                //     transformRequest: [(data, headers) => {
                //         delete headers.common['Workspace-ID'];
                //         return data;
                //     }]
                // }}
            />
        </Dialog>
    )
}

export default WorkspaceModulesDialog;