import { Icon as Iconify } from "@iconify/react";
import Extension from "@mui/icons-material/Extension";
import { Avatar, Box, Button, ListItemAvatar, ListItemText, Stack } from "@mui/material";
import format from "date-fns-jalali/format";
import differenceInDays from "date-fns/differenceInDays";
import { useState } from "react";
import { useOutletContext, useParams, useSearchParams } from "react-router-dom";

import { useMiddleware } from "../../../../Application";
import ResponsiveTable from "../../../../components/ResponsiveTable";
import Text from "../../../../components/Text";
import useGetRowNumber from "../../../../utils/useGetRowNumber";
import useResponsiveTableFetch from "../../../../utils/useResponsiveTableFetch";
import WorkspaceModulesDialog from "../../components/WorkspaceModulesDialog";


const Subscriptions = () => {

    const { workspace } = useOutletContext();
    const { workspaceId } = useParams();

    const middleware = useMiddleware('core.Workspaces');

    const [searchParams, setSearchParams] = useSearchParams();

    const { data, total, loading, loadMore, reload } = useResponsiveTableFetch({
        handleFetch: params => middleware.subscriptions(workspaceId, {
            ...params,
            ...Object.fromEntries([...searchParams])
        }),
        loadDeps: [searchParams],
        infiniteScrollDefaultParams: {
            page: searchParams.has('page') ? searchParams.get('page') : 0,
            perPage: searchParams.has('perPage') ? searchParams.get('perPage') : 25,
        }
    });

    const getRowNumber = useGetRowNumber();

    const [modulesDialogProps, setModulesDialogProps] = useState({ open: false });

    return (
        <>
            <Box
                sx={{
                    display: "flex",
                    mb: { xs: 0, sm: 1 },
                    px: 3,
                    py: 2,
                }}
            >
                <Box sx={{ flexGrow: 1 }} />
                <Stack direction="row" spacing={1}>
                    <Button
                        variant="contained"
                        startIcon={<Iconify icon="solar:add-circle-bold" />}
                        onClick={() => setModulesDialogProps({ open: true })}
                    >اشتراک جدید</Button>
                </Stack>
            </Box>
            <ResponsiveTable
                data={data}
                loading={loading}
                meta={{ total }}
                infiniteScrollProps={{
                    loading: false,
                    hasNextPage: data.length < total,
                    onLoadMore: loadMore
                }}
                definitions={[
                    {
                        title: 'ردیف',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center"
                        },
                        renderCell: (row, index) => getRowNumber(index)
                    }, {
                        renderCell: row => (
                            <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                <Avatar
                                    variant="rounded"
                                    alt={row.subscribed.identifier}
                                    src={row.subscribed.picture}
                                    sx={{ mr: 1 }}
                                >
                                    <Extension />
                                </Avatar>
                                <Box>
                                    <strong>{row.subscribed.name}</strong>
                                </Box>
                            </Box>
                        )
                    },
                    {
                        title: 'تاریخ شروع',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center"
                        },
                        render: (row, index) => row.validFrom ? <Text>{format(new Date(row.validFrom), 'yyyy/MM/dd hh:mm')}</Text> : '-'
                    },
                    {
                        title: 'تاریخ اعتبار',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center"
                        },
                        render: (row, index) => row.validTill ? <Text>{format(new Date(row.validTill), 'yyyy/MM/dd hh:mm')}</Text> : '∞'
                    },
                    {
                        title: 'زمان باقیمانده',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center"
                        },
                        render: (row, index) => row.validTill ? <span><Text>{differenceInDays(new Date(row.validTill), new Date())}</Text> <small>روز</small></span> : '∞'
                    },
                ]}
                renderSheetTitle={row => row.subscribed?.name}
                renderListItemChildren={(row, index) => (
                    <>
                        <ListItemAvatar>
                            <Avatar
                                variant="rounded"
                                alt={row.subscribed.identifier}
                                src={row.subscribed.picture}
                            ><Extension /></Avatar>
                        </ListItemAvatar>
                        <ListItemText
                            primary={<strong>{row.subscribed.name}</strong>}
                        />
                    </>
                )}

                listItemProps={{ disablePadding: false }}

                // tableRowProps={row => ({
                //     hover: true,
                //     striped: true,
                //     onClick: e => navigate(
                //         '/case/'.concat(row.id),
                //         { state: { prev: location } }
                //     )
                // })}

                paginationProps={{
                    count: total,
                    rowsPerPage: searchParams.get('perPage') || 25,
                    page: searchParams.get('page') || 0,
                    onPageChange: (e, page) => setSearchParams({ ...Object.fromEntries([...searchParams]), page }),
                    onRowsPerPageChange: e => setSearchParams({ ...Object.fromEntries([...searchParams]), perPage: e.target.value })
                }}

            // actions={[
            //     { title: 'ویرایش', icon: <Edit />, onClick: onClickEdit },
            //     { title: 'بایگانی', icon: <ArchiveOutlined />, onClick: row => alert('Archived clicked') },
            //     { title: 'انتقال به زباله‌دان', icon: <Delete />, onClick: onClickDelete }
            // ]}
            />

            <WorkspaceModulesDialog
                workspace={workspace}
                {...modulesDialogProps}
                onClose={() => setModulesDialogProps({ open: false })}
                onSave={() => {
                    reload();
                    setModulesDialogProps({ open: false })
                }}
            />
        </>
    )
}

export default Subscriptions;